	module.exports = {"data":{"site":{"siteMetadata":{"title":"Glen Cheney"}},"markdownRemark":{"html":"<p>Drag and drop css minification with cssnano.</p>\n<h2>Convenient CSS minification</h2>\n<p>I built this small app because sometimes I need to minify CSS outside of a build system, for example, inlining CSS <a href=\"https://github.com/necolas/normalize.css\">normalize.css</a> into the <code class=\"language-text\">&lt;head&gt;</code> of a document. Other online CSS minifiers already exist, but none that use <a href=\"https://cssnano.co/\">cssnano</a>. cssnano was packaged with webpack’s <a href=\"https://github.com/webpack-contrib/css-loader\">css-loader</a> until it July 2018.</p>\n<h2>Tech details</h2>\n<p>Unfortunately, even though <code class=\"language-text\">postcss</code> works in the browser, the <code class=\"language-text\">cssnano</code> package does not. After you drop a CSS file onto the page, a request is sent to the server to minify the CSS. <a href=\"https://codemirror.net/\">CodeMirror</a> then renders the response in a fullscreen editor, giving users the ability to copy the styles which were just minified.</p>\n<p>The frontend is built with TypeScript and bundled with webpack. Since the <code class=\"language-text\">codemirror</code> and <code class=\"language-text\">clipboard</code> packages are only used after the user adds some CSS to minify, I use webpack’s code-splitting with dynamic imports to load them after the app has initialized.</p>\n<h2>CSS OMG</h2>\n<p>The UX of this app is inspired by <a href=\"https://jakearchibald.github.io/svgomg/\">SVGOMG</a> by Jake Archibald. I use SVGOMG all the time and being able to both paste code to minify and drag-n-drop a file is very handy.</p>\n<!-- markdownlint-disable MD033 -->\n<video muted playsinline controls loop poster=\"/cssnano-minifier-poster.png\">\n  <source src=\"/static/cssnano-minifier-b86cd4b830a89c48e2bc8e37ad34b452.webm\" type=\"video/webm; codecs=vp9,vorbis\">\n  <source src=\"/static/cssnano-minifier-34aa992304e71cf1d2fc0db2844291a9.mp4\" type=\"video/mp4\">\n</video>","frontmatter":{"title":"cssnano","imageDescription":"Landing page for the cssnano minifier application.","href":"https://cssnano.herokuapp.com/","tags":["open source","drag-n-drop","TypeScript","Node"]}},"hero":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.7768178545716342,"src":"/static/cssnano-minifier-917f586e981001271bbea4790340b00e-feda4.png","srcSet":"/static/cssnano-minifier-917f586e981001271bbea4790340b00e-a2512.png 640w,\n/static/cssnano-minifier-917f586e981001271bbea4790340b00e-64657.png 1280w,\n/static/cssnano-minifier-917f586e981001271bbea4790340b00e-feda4.png 2468w","srcWebp":"/static/cssnano-minifier-917f586e981001271bbea4790340b00e-af4f7.webp","srcSetWebp":"/static/cssnano-minifier-917f586e981001271bbea4790340b00e-618a9.webp 640w,\n/static/cssnano-minifier-917f586e981001271bbea4790340b00e-f0967.webp 1280w,\n/static/cssnano-minifier-917f586e981001271bbea4790340b00e-af4f7.webp 2468w","sizes":"(max-width: 2468px) 100vw, 2468px"}}},"pathContext":{"slug":"/cssnano-minifier/","heroImage":"/images/cssnano-minifier.png/","nextProject":{"frontmatter":{"title":"hennessy.com"},"fields":{"slug":"/hennessy/"}}}}

